.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important; }

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }

.nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid; }

.tabs ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-gap: 40px; }

.tabs li {
